// Call plugin.
import pluginInterface from '../interfaceNew'
import * as sagas from './sagas'
import events from '../interfaceNew/events'

// Other plugins.
import { mapEvents } from '../../events/interface/actions'
import { update as updateConfig } from '../../config/interface/actions'
import { sanitizeSdesFromSdp, modifySdpBandwidth } from '../../callstack/utils'
import { logManager } from '../../logs'

// Helpers.
import { mergeValues, autoRestart } from '../../common/utils'
import createCodecRemover from 'fcs/src/js/sdp/codecRemover'

// Libraries.
import { values } from 'lodash/fp'
import { fork, put } from 'redux-saga/effects'
import { getWebRTCSupportCapabilities } from 'kandy-webrtc'

const log = logManager.getLogger('CALL')

// eslint-disable-next-line no-warning-comments
/**
 * Configuration options for the call feature.
 * @public
 * @name config.call
 * @memberof config
 * @instance
 * @param {Object} call The call configuration object.
 * @param {string} [call.sdpSemantics='unified-plan'] The sdpSemantics to use (`'unified-plan'` or `'plan-b'`).
 * @param {Array<call.IceServer>} [call.iceServers] The list of ICE servers to be used for calls.
 * @param {number} [call.iceCollectionDelay=1000] Time, in milliseconds, to delay in between
 *    ICE candidate checks. If ICE collection does not complete normally, the SDK will check
 *    collected candidates at this interval to determine if the operation can continue.
 * @param {number} [call.maxIceTimeout=3000] Maximum time, in milliseconds, to wait for ICE
 *    collection to complete normally. After this time, the process will timeout and the
 *    operation will attempt to continue no matter how many candidates have been collected.
 * @param {Function} [call.iceCollectionCheck] Function to check whether collected candidates
 *    can be used to continue the operation. The function will receive an array of ICE
 *    candidates and must return a boolean value of whether the SDK should attempt to continue
 *    the operation. By default, the check is to ensure at least one relay candidate has been
 *    collected.
 * @param {boolean} [call.serverTurnCredentials=true] Whether server-provided TURN credentials should be used.
 * @param {Array<call.SdpHandlerFunction>} [call.sdpHandlers] List of SDP handler functions to modify SDP. Advanced usage.
 * @param {boolean} [call.removeH264Codecs=true] Whether to remove "H264" codec lines from incoming and outgoing SDP messages.
 * @param {boolean} [call.earlyMedia=false] Whether early media should be supported for calls.
 * @param {boolean} [call.resyncOnConnect=false] Whether the SDK should re-sync all call states after connecting (requires Kandy Link 4.7.1+).
 */
export default function callsLink (options = {}) {
  const { mediaDevices, peerConnection } = getWebRTCSupportCapabilities()
  if (!mediaDevices || !peerConnection) {
    log.warn('Calls are not supported on this platform due to lack of WebRTC support. Call APIs will not be available.')
    return
  }

  // Check for WebRTC support.
  const defaultOptions = {
    // The list of TURN/STUN servers to use.
    iceServers: [],
    // Time in ms to what between checking ICE candidates during negotiation.
    iceCollectionDelay: 1000,
    // The maximum time, in ms, to wait before timing out ICE collection.
    maxIceTimeout: 3000,
    // TODO: Remove this once all the browsers use unified-plan
    sdpSemantics: 'plan-b',
    // Whether the SDK should fetch turn credentials.
    serverTurnCredentials: true,
    // Trickle ICE method to use for calls.
    trickleIceMode: 'NONE',
    // SDP handlers to be included in the pipeline for every operation.
    sdpHandlers: [],
    // filter out H264 Codec
    removeH264Codecs: true,
    // Set this to false to bypass sip address normalization
    normalizeDestination: true,
    earlyMedia: false,
    // Defaults set by the Webrtc stack:
    //    iceCollectionCheck: Has at least one relay candidate.
    resyncOnConnect: false
  }

  // For backwards compatibility between old ICE config and new config.
  if (options.iceserver && !options.iceServers) {
    log.warn('Call configuration `iceserver` is being replaced by `iceServers`. Please update to the config.')
    options.iceServers = options.iceserver
    delete options.iceserver
  }

  options = mergeValues(defaultOptions, options)

  function * init ({ webRTC }) {
    // Change sdpSemantics if not supported
    const isPlanB = options.sdpSemantics === 'plan-b'
    const isNotChrome = webRTC.getBrowserDetails().browser !== 'chrome'
    if (isPlanB && isNotChrome) {
      log.warn('Only Chrome supports `plan-b` sdpSemantics. Switching to `unified-plan`.')
      options.sdpSemantics = 'unified-plan'
    }

    yield put(updateConfig(options, pluginInterface.name))
    yield put(mapEvents(events))

    /*
     * Set SDP handlers to be used for every operation:
     *
     * 1. Application provided SDP handlers.
     *
     * 2. Disable DTLS-SDES crypto method (ie. delete the line) if there's a better
     *    crypto method enabled. WebRTC only allows one method to be enabled.
     *    This is needed for interoperability with non-browser endpoints that include
     *    SDES as a fallback method.
     *
     * 3. [optional] Disable H264 Codecs for video calls, used to reduce SDP size
     *
     * 4. Modify sdp and add bandwidth limits on it if bandwidth controls are provided.
     */
    let sdpHandlers = options.sdpHandlers
    if (options.removeH264Codecs) {
      sdpHandlers.push(createCodecRemover(['H264']))
    }
    sdpHandlers.push(sanitizeSdesFromSdp)
    sdpHandlers.push(modifySdpBandwidth)

    // Dependencies to be provided to every call saga.
    const deps = {
      webRTC: webRTC.managers,
      sdpHandlers
    }

    // Wrap the call sagas in a function that provides them with the webRTC stack.
    const wrappedSagas = values(sagas).map(saga => {
      return autoRestart(() => saga(deps))
    })

    // Run all of the sagas.
    for (let saga of wrappedSagas) {
      yield fork(saga)
    }
  }

  return {
    name: pluginInterface.name,
    api: pluginInterface.api,
    reducer: pluginInterface.reducer,
    init,
    capabilities: ['call', 'link_call', 'link_user_id', 'link_pstn']
  }
}
